<template>
    <div id="app">
        <div>
            <router-view></router-view>
        </div>
        <div id="toast" v-show="showLayer==true">
            <div class="weui-mask_transparent"></div>
            <div class="weui-toast">
                <p class="weui-toast__content">,</p>
            </div>
        </div>
    </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  name: 'form',
  data () {
    return {

    }
  },
  computed:{
    ...mapState({
      showLayer:state=>state.showLayer
    }),
  },
  methods:{

  }
}
</script>

<style lang="scss" scoped>
.weui-toast{
    width:8rem;
    height:6rem;
    min-height:6rem;
}
.weui-toast__content{
    line-height:6rem;
}
</style>


