<template>
	<div>
		<div class="page msg_warn js_show">
		    <div class="weui-msg">
		        <div class="weui-msg__icon-area"><i class="weui-icon-warn weui-icon_msg"></i></div>
		        <div class="weui-msg__text-area">
		            <h2 class="weui-msg__title"></h2>
		            <p class="weui-msg__desc">{{ajText}}</p>
		        </div>
		        <div class="weui-msg__opr-area">
		            <p class="weui-btn-area">
		                <a class="weui-btn weui-btn_primary" @click="backInput"></a>
		            </p>
		        </div>
		    </div>
		</div>
	</div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  name: 'fail',
  data () {
    return {

    }
  },
mounted: function () {

},
  computed:{
    ...mapState({
      ajText:state=>state.ajText
    }),
  },
  methods:{
  	backInput(){
  		wx.closeWindow();
  	}
  }
}
</script>


