<template>
  <div>
    <div class="page__hd">
      <div class="page__title">
        
      </div>
      <div class="weui-cells__title" style="padding-left:0"></div>
    </div>
    <div class="weui-cells">
      <div class="weui-cell">
        <div class="weui-cell__hd">
          <label class="weui-label"></label>
        </div>
        <div class="weui-cell__bd">
          <input class="weui-input" type="date" name="dateStart"  :value="searchInfo.dateStart" @input="handleChange">
        </div>
      </div>
    </div>
    <div class="weui-cells">
      <div class="weui-cell">
        <div class="weui-cell__hd">
          <label class="weui-label"></label>
        </div>
        <div class="weui-cell__bd">
          <input class="weui-input" type="date" name="dateEnd"  :value="searchInfo.dateEnd" @input="handleChange">
        </div>
      </div>
    </div>
    <div class="weui-cells">
      <div class="weui-cell">
        <div class="weui-cell__hd">
          <label class="weui-label"></label>
        </div>
        <div class="weui-cell__bd">
          <input class="weui-input" type="text" name="client_name" placeholder=""  :value="searchInfo.client_name" @input="handleChange">
        </div>
      </div>
    </div>
    <div class="weui-cells">
      <div class="weui-cell">
        <div class="weui-cell__hd">
          <label class="weui-label"></label>
        </div>
        <div class="weui-cell__bd">
          <input class="weui-input" type="tel" name="phone"  placeholder=""  :value="searchInfo.phone" @input="handleChange">
        </div>
      </div>
    </div>
    <div class="page__bd_spacing">
      <a href="javascript:;" class="weui-btn weui-btn_primary" @click="search"></a>
    </div>
    <div v-if="curPageData.length!=0">
      <form-table :content="curPageData"></form-table> 
      <div class="weui-loadmore" v-if="curPage!=allDataCount">
              <i class="weui-loading"></i>
              <span class="weui-loadmore__tips"></span>
      </div>
      <div v-else class="weui-loadmore weui-loadmore_line">
              <span class="weui-loadmore__tips"></span>
      </div>
    </div>
    <div v-else>
      <div class="weui-loadmore weui-loadmore_line">
              <span class="weui-loadmore__tips"></span>
      </div>     
    </div>
      <div id="loadingToast" style="/* opacity: 0; *//* display: none; */" v-if="loading==true">
        <div class="weui-mask_transparent"></div>
        <div class="weui-toast">
            <i class="weui-loading weui-icon_toast"></i>
            <p class="weui-toast__content"></p>
        </div>
      </div>
  </div>
</template>
<script>
import formTable from '../components/formTable.vue'
import { mapState,mapMutations,mapActions } from 'vuex'

export default {
  name: 'search',
  data () {
    return {
    }
  },
  components: {
      formTable
  },
  beforeMount(){
    window.addEventListener("scroll",this.scrollFuc);
  },
  beforeDestroy(){
    window.removeEventListener("scroll",this.scrollFuc);
  },
  created(){
    this.search();
  },
  computed:{
    ...mapState({
      loading:state=>state.search.loading,
      searchInfo:state=>state.search.searchInfo,
      curPageData:state=>state.search.curPageData,
      curPage:state=>state.search.curPage,
      allDataCount:state=>state.search.allDataCount
    })
  },
  methods:{
    handleChange(e){
      let searchInfo = Object.assign({},this.searchInfo);
      searchInfo[e.target.name] = e.target.value;
      this.update(searchInfo);
    },
    closeDialog(){
      this.$refs.dialog.style.display = "none";
    },
    search(){
      this.search();
    },
    scrollFuc(){
      if(window.innerHeight+window.scrollY>=document.body.scrollHeight){
        if(this.curPage==this.allDataCount) return;
        this.add();
      }
    },
    ...mapMutations({
      update:"updateSearch",
      search:"updateData"
    }),
    ...mapActions({
      search: 'search',
      add: 'add'
    })
  }
}
</script>
<style lang="scss" scoped>
.page__hd{
  padding:10px;
  padding-bottom:10px;
}
.weui-cells{
  margin-top:0px;

}
.page__bd_spacing{
  margin-top:20px;
}
.weui-loadmore_line .weui-loadmore__tips{
  background:none;
}
</style>


