
<template>
	<div>
	    <div class="page__hd">
	      <div class="page__title">
	        
	      </div>
	    </div>
	    <div class="page__bd">
			<div class="weui-form-preview">
	            <div class="weui-form-preview__hd">
	                <div class="weui-form-preview__item">
	                    <label class="weui-form-preview__label"></label>
	                    <em class="weui-form-preview__value">{{formInfo.client_name}}</em>
	                </div>
	            </div>
	            <div class="weui-form-preview__hd">
	                <div class="weui-form-preview__item">
	                    <label class="weui-form-preview__label"></label>
	                    <em class="weui-form-preview__value">{{formInfo.phone}}</em>
	                </div>
	            </div>
	            <div class="weui-form-preview__hd">
	                <div class="weui-form-preview__item">
	                    <label class="weui-form-preview__label"></label>
	                    <span class="weui-form-preview__value">{{formInfo.address}}</span>
	                </div>
	            </div>
	            <div class="weui-form-preview__hd">
	                <div class="weui-form-preview__item">
	                    <label class="weui-form-preview__label"></label>
	                    <span class="weui-form-preview__value">{{formInfo.content}}</span>
	                </div>
	            </div>
				<div class="weui-form-preview__ft">
	                <a class="weui-form-preview__btn weui-form-preview__btn_default" @click="back"></a>
	                <button type="submit" class="weui-form-preview__btn weui-form-preview__btn_primary" href="javascript:" @click="submit"></button>
	            </div>
		    </div>
		</div>
	</div>
</template>
<script>
import { mapState,mapMutations,mapActions } from 'vuex'
export default {
  name: 'submit',
  data () {
    return {

    }
  },
  computed:{
    ...mapState({
      formInfo:state=>state.form.formInfo
    }),
  },
  methods:{
  	back(){
  		this.$router.push({path:"/input"});
  	},
  	submit(){
  		this.submit();
  	},
    ...mapActions({
      submit: 'submit' 
    })
  }
}
</script>
<style lang="scss" scoped>
	.weui-form-preview__hd .weui-form-preview__value{
		font-size:1rem;
	}
</style>


