import fetch from 'isomorphic-fetch';

const state = {
	loading:false,
	initial:false,
    formInfo: {
    	open_id:"",
    	client_name:"",
    	phone:"",
    	address:"",
    	content:"",
      voiceId:0
    }
}

const actions = {
    submit({commit,state}){
    	commit("addLoading");
    	return fetch("/weixin/aj/saveOrder",
    		{
    			method:"POST",
				  headers: {
				    'Content-Type': 'application/x-www-form-urlencoded'
				  },
    			body:"formInfo="+JSON.stringify(state.formInfo)
    		}
    		)
          .then(response => response.json()).then(res=>{
          		commit("hideLoading");
          		if(res.code=="100000"){
          			location.href="/#/success";     			
          		}else{
          			commit("changeAjText",res.msg);
          				location.href="/#/fail";
          			

          		}
          		
            }
    	);	
    },
    getFormInfo({commit,state}){
    	commit("addLoading");
    	return fetch("/weixin/aj/initOrderForm"+window.location.search)
          .then(response => response.json()).then(res=>{
          		commit("hideLoading");
                if(res.code=="100000"){
                	let formInfo = {
                		open_id:res.data.open_id,
				    	client_name:res.data.client_name,
				    	phone:res.data.phone,
				    	address:res.data.address,
				    	content:res.data.content             		
                	};
                    commit("updateForm",formInfo);
                }
          		
            }
    	);	
    }

}

const getters = {
    getFormInfo: state => state.formInfo
}

const mutations = {
	["hideLoading"](state){
		state.loading = false;
	},
	["addLoading"](state){
		state.loading = true;
	},
	["updateForm"](state,res){
		state.initial = true;
		state.formInfo = res;
	}

}

export default {
    state,
    actions,
    getters,
    mutations
}


