import Vue from 'vue';
import Router from 'vue-router';
import formPage from './views/form.vue'
import submitPage from './views/submit.vue'
import successPage from './views/success.vue'
import failPage from './views/fail.vue'
import searchPage from './views/search.vue'
Vue.use(Router)

export default new Router({
    linkActiveClass: 'active',
    routes:[
        {
            path:'/input',
            component:formPage
        },
        {
            path:'/submit',
            component:submitPage
        },
        {
            path:'/success',
            component:successPage
        },
        {
            path:'/fail',
            component:failPage
        },
        {
            path:'/search',
            component:searchPage
        },
        {
            path:'*',
            component:formPage
        }
    ]
})


