<template>
	<div class="form-table">
		<table>
			<thead>
				<tr>
					<th></th>
					<th></th>
					<th>	</th>
					<th></th>		
				</tr>
			</thead>
			<tbody>
				<tr v-for="(item,index) in content" :key="index">
					<td>{{index+1}}</td>
					<td>{{item.client_name}}</td>
					<td>{{item.phone}}</td>
					<td class="click-detail" :index="index" @click="showDetail"></td>
				</tr>
			</tbody>
		</table>
	    <div class="js_dialog" id="iosDialog2" style="display: none;" ref="dialog">
	            <div class="weui-mask"></div>
	                <div class="weui-dialog">
	                    <div class="weui-dialog__bd">
				            <div class="weui-form-preview__hd">
				                <div class="weui-form-preview__item">
				                    <label class="weui-form-preview__label"></label>
				                    <em class="weui-form-preview__value">{{dialog.client_name}}</em>
				                </div>
				            </div>
				            <div class="weui-form-preview__hd">
				                <div class="weui-form-preview__item">
				                    <label class="weui-form-preview__label"></label>
				                    <em class="weui-form-preview__value">{{dialog.phone}}</em>
				                </div>
				            </div>
				            <div class="weui-form-preview__hd">
				                <div class="weui-form-preview__item">
				                    <label class="weui-form-preview__label"></label>
				                    <span class="weui-form-preview__value" >{{dialog.address}}</span>
				                </div>
				            </div>
				            <div class="weui-form-preview__hd">
				                <div class="weui-form-preview__item">
				                    <label class="weui-form-preview__label"></label>
				                    <span class="weui-form-preview__value">{{dialog.content}}</span>
				                </div>
				            </div>
	                    </div>
	                    <div class="weui-dialog__ft">
	                        <a href="javascript:;" class="weui-dialog__btn weui-dialog__btn_primary" @click="closeDialog"></a>
	                    </div>
	                </div>
	            </div>
	      	</div>
	</div>
</template>
<script>
export default {
  data () {
    return {
    	dialog:{
    		client_name:"",
    		phone:"",
    		address:"",
    		content:""
    	}
    }
  },
  props:{
  	content:Array
  },
  methods:{
  	showDetail(e){
  		let index = e.target.getAttribute("index");
  		console.log(index);
  		console.log(JSON.stringify(this.content[index]));
  		this.dialog = JSON.parse(JSON.stringify(this.content[index]));
  		this.$refs.dialog.style.display = "block";
  	},
    closeDialog(){
      this.$refs.dialog.style.display = "none";
    }
  }
}
</script>
<style lang="scss" scoped>
.form-table{
	width:100%;
}
.form-table table{
	width:100%;
	margin-top:20px;
	padding:20px;
	border-top:1px solid #ccc;
}
.form-table table tr{
	text-align:center;
}
.form-table table th,.form-table table td{
	padding:10px 0px;
}
.form-table table td,.form-table table th{
	border-bottom:1px solid #ccc;
}
.click-detail{
	color:#1AAD19;
}
.weui-dialog{
	max-width:90%;
}
.weui-form-preview__hd .weui-form-preview__value{
	font-size:1rem;
}
.weui-form-preview__hd:after {
	border:none
}
</style>


