import fetch from 'isomorphic-fetch';

function getYesterDay(date){
    var yesterday = new Date(Date.parse(date)-1000*60*60*24);
    var year = yesterday.getFullYear();
    var month = yesterday.getMonth()+1<10?"0"+(yesterday.getMonth()+1):(yesterday.getMonth()+1);
    var date = yesterday.getDate()<10?"0"+yesterday.getDate():yesterday.getDate();
    return year+"-"+month+"-"+date;
}
const state = {
    loading:true,
    searchInfo:{
        dateStart:getYesterDay(new Date()),
        dateEnd:getYesterDay(new Date()),
        client_name:"",
        phone:""
    },
    curPageData:[],
    curPage:1,
    allDataCount:1,
    pageSize:10
}

const actions = {
    search({ dispatch,commit,state}){
        commit("addLoading");
    	return fetch("/weixin/aj/listOrderByParams?dateStart="+state.searchInfo.dateStart+"&dateEnd="+state.searchInfo.dateEnd+"&client_name="+state.searchInfo.client_name+"&phone="+state.searchInfo.phone+"&curPage="+state.curPage+"&pageSize="+state.pageSize)
          .then(response => response.json()).then(res=>{
                if(res.code=="100000"){
                    commit("updateData",res.data);
                }else{
                    dispatch("showWarn");
                }
          		
            }
    	);	
    },
    add({commit,state}){
        commit("addLoading");
        return fetch("/weixin/aj/listOrderByParams?dateStart="+state.searchInfo.dateStart+"&dateEnd="+state.searchInfo.dateEnd+"&client_name="+state.searchInfo.client_name+"&phone="+state.searchInfo.phone+"&curPage="+state.curPage+"&pageSize="+state.pageSize)
          .then(response => response.json()).then(res=>{
                if(res.code=="100000"){
                    commit("addData",res.data);
                }else{
                    dispatch("showWarn");
                }
               
            }
        );  
    }
}

const getters = {

}

const mutations = {
    ["addLoading"](state){
        state.loading = true;
    },
	["updateData"](state,res){
        state.loading = false;
		state.curPageData = res.curPageData;
        state.allDataCount = res.allDataCount;
	},
    ["addData"](state,res){
        state.loading = false;
        state.curPage = state.curPage+1;
        state.curPageData = state.curPageData.concat(res.curPageData);
        state.allDataCount = res.allDataCount;
    },
    ["updateSearch"](state,res){
        state.searchInfo = res;
    },
    ["updatePage"](state){
        state.curPage =1;
    }
}

export default {
    state,
    actions,
    getters,
    mutations
}


