import Vue from 'vue'
import vuex from 'vuex'
import form  from './module/form'
import search  from './module/search'
Vue.use(vuex);

export default new vuex.Store({
    state:{
        ajText:"",
        showLayer:false
    },
	mutations:{
		["changeAjText"](state,res){
			state.ajText = res;
		},
		["showToast"](state,res){
			state.showLayer = true;
		},
		["hideToast"](state,res){
			state.showLayer = false;
		}	

	},
	actions:{
		showWarn({commit,state}){
			commit("showToast");
			setTimeout(()=>{
				commit("hideToast");
			},1000);
		}
	},
    modules: {
    	form,
    	search
    }
})


