//index.vue
<template>
  <div>
    <div class="page__hd">
      <div class="weui-cells__title" style="padding-left:0"></div>
    </div>
    <div class="weui-cells">
      <div class="weui-cell">
        <div class="weui-cell__hd">
          <label class="weui-label"></label>
        </div>
        <div class="weui-cell__bd">
          <input class="weui-input" type="text" name="client_name" placeholder="" :value="formInfo.client_name" @input="handleChange">
        </div>
      </div>
    </div>
    <div class="weui-cells">
      <div class="weui-cell">
        <div class="weui-cell__hd">
          <label class="weui-label"></label>
        </div>
        <div class="weui-cell__bd">
          <input class="weui-input" type="tel" name="phone" pattern="[0-9]{0,11}" placeholder=""  :value="formInfo.phone" @input="handleChange">
        </div>
      </div>
    </div>
    <div class="weui-cells__title"></div>
    <div class="weui-cells weui-cells_form">
      <div class="weui-cell">
        <div class="weui-cell__bd">
          <textarea class="weui-textarea" name="address" placeholder="" rows="3" :value="formInfo.address" @input="handleChange"></textarea>
        </div>
      </div>
    </div>
    <div class="weui-cells__title"></div>
    <div class="weui-cells weui-cells_form">
      <div class="weui-cell">
        <div class="weui-cell__bd">
          <textarea class="weui-textarea" name="content" placeholder="" rows="3" :value="formInfo.content" @input="handleChange"></textarea>
        </div>
      </div>
    </div>
    <div style="margin-top:10px;overflow:hidden">
    <div @touchstart="startVoice" @touchend="endVoice" style="display:inline-block;padding:10px 20px;float:left;"></div>
    <div @touchstart="startPlay"  style="display:inline-block;padding:10px 20px;float:right;"></div>
    </div>
    <div class="page__bd_spacing">
      <a href="javascript:;" class="weui-btn weui-btn_primary" @click="nextSetp" ></a>
    </div>  
    <div class="js_dialog" id="iosDialog2" style=" display: none;" ref="dialog">
                <div class="weui-mask"></div>
                <div class="weui-dialog">
                    <div class="weui-dialog__bd">{{this.dialog_content}}</div>
                    <div class="weui-dialog__ft">
                        <a href="javascript:;" class="weui-dialog__btn weui-dialog__btn_primary" @click="closeDialog"></a>
                    </div>
                </div>
            </div>
      <div id="loadingToast"  v-if="loading==true">
        <div class="weui-mask_transparent"></div>
        <div class="weui-toast">
            <i class="weui-loading weui-icon_toast"></i>
            <p class="weui-toast__content"></p>
        </div>
      </div>
      </div>
</template>

<script>
import { mapState,mapMutations,mapActions  } from 'vuex'
export default {
  name: 'form',
  data () {
    return {
      dialog_content:""
    }
  },
  created(){
    if(!this.initial){
      this.getFormInfo();
    }

  },
  computed:{
    ...mapState({
      loading:state=>state.form.loading,
      initial:state=>state.form.initial,
      formInfo:state=>state.form.formInfo
    }),
  },
  methods:{
    handleChange(e){
      let formInfo = Object.assign({},this.formInfo);
      formInfo[e.target.name] = e.target.value;
      this.update(formInfo);
    },
    nextSetp(){
      let formInfo = Object.assign({},this.formInfo);
      let checked = true;
      if(formInfo.client_name.replace(/(^\s*)|(\s*$)/g, "")==""||formInfo.phone==""||formInfo.address.replace(/(^\s*)|(\s*$)/g, "")==""||formInfo.content.replace(/(^\s*)|(\s*$)/g, "")==""){
        this.dialog_content = "";
        this.$refs.dialog.style.display = "block";
        return;
      }

      if(formInfo.client_name.replace(/[^\x00-\xff]/g, "rr").length>20){
        this.dialog_content = "10";
        this.$refs.dialog.style.display = "block";
        return;
      }
      if(formInfo.phone.length>11){
        this.dialog_content = "11";
        this.$refs.dialog.style.display = "block";
        return;       
      }

      if(formInfo.address.replace(/[^\x00-\xff]/g, "rr").length>100){
        this.dialog_content = "50";
        this.$refs.dialog.style.display = "block";
        return;       
      }


      this.$router.push({path:"/submit"});
      
    },
    closeDialog(){
      this.$refs.dialog.style.display = "none";
    },
    startVoice(){
      wx.startRecord();
    },
    endVoice(){
      let _this = this;
      wx.stopRecord({
          success: function (res) {
            let localId = res.localId;
            let formInfo = Object.assign({},this.formInfo);
            formInfo["voiceId"] = localId;
            _this.update(formInfo);
          }
      });
    },
    startPlay(){
      wx.playVoice({
          localId: this.formInfo.voiceId // IDstopRecord
      });
    },
    ...mapMutations({
      update:"updateForm"
    }),
    ...mapActions({
      getFormInfo: 'getFormInfo'
    })
  }
}
</script>
<style lang="scss" scoped>
  .page__hd{
    padding:10px;
    padding-bottom:0;
  }

  .page__bd_spacing{
    margin-top:50px;
  }
</style>


